.contacts {
  display: inline-block;
}

.contact {
  display: flex;
  text-align: justify;
  margin: auto;
  text-decoration: wavy;
}

.text {
  font-size: 20px;
  font-weight: 600;
  background-image: linear-gradient(to left, #010003, #b393d3);
  color: black;
  background-clip: text;
  margin-bottom: 20px;
  margin-right: 25px;
}

.btnDel {
  justify-content: center;
  display: block;
  box-sizing: border-box;
  padding: 0 15px;
  margin: auto;
  outline: none;
  border: 1px solid #fff;
  border-radius: 50px;
  height: 46px;
  line-height: 46px;
  font-size: 14px;
  font-weight: 600;
  text-decoration: none;
  color: #444;
  background-color: #fff;
  box-shadow: 0 4px 6px rgb(65 132 144 / 10%), 0 1px 3px rgb(0 0 0 / 8%);
  cursor: pointer;
}
